*, :before, :after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
  tab-size: 4;
  font-family: system-ui, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
  line-height: 1.15;
}

body {
  margin: 0;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.header {
  height: 125px;
  width: 100%;
  z-index: 100;
  background-image: url("header-background-mobile.a8ceff09.jpg");
  background-repeat: no-repeat;
  background-size: 100vw 125px;
  justify-content: center;
  align-items: center;
  margin-bottom: 0;
  font-size: 12px;
  transition: all .5s ease-in-out;
  display: flex;
  position: sticky;
  top: 0;
}

.header:hover {
  height: 216px;
}

.header--library {
  background-image: url("my-library-mobile.f70bf307.jpg");
}

.header-buttons {
  gap: 20px;
}

.header-buttons > button {
  cursor: pointer;
  height: 44px;
  text-transform: uppercase;
  width: 130px;
  background: none;
  border: 1px solid #fff;
  border-radius: 5px;
  margin-top: 70px;
}

.header-buttons > button:first-of-type {
  margin-right: 15px;
}

.header-buttons--active {
  background: #ff6b08 !important;
}

.header__container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 20px 20px 40px;
  display: flex;
}

.header__nav {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  transition: all .5s ease-in-out;
  display: flex;
}

.header__nav-icon {
  stroke: #fff;
  margin-right: 15px;
  transition: all .5s ease-in-out;
}

.header__nav-icon:hover {
  stroke: #000;
}

.header__nav-title {
  display: none;
}

.header__nav-language {
  width: 100%;
  justify-content: flex-end;
  align-items: center;
  gap: 10px;
  padding-bottom: 20px;
  display: flex;
}

.header__nav-language-item {
  color: #fff;
  font-size: 8px;
}

.header__nav-language-btn {
  width: 18px;
  height: 18px;
  color: #fff;
  text-transform: uppercase;
  background-color: #0000;
  border: 1px solid #fff;
  border-radius: 5px;
  font-size: 10px;
}

.header__nav-language-btn:hover, .header__nav-language-btn:focus {
  filter: drop-shadow(0 8px 43px #ff6b0199);
  background-color: #ff6b08;
  border-color: #ff6b08;
  transition: all .5s;
}

.header__nav-container {
  width: 280px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__nav-list {
  align-items: center;
  gap: 40px;
  list-style: none;
  display: flex;
}

.header__nav-list-item > a {
  color: #fff;
  text-transform: uppercase;
  line-height: 1.5;
  text-decoration: none;
}

.header__nav-list-item > a:hover, .header__nav-list-item > a:focus {
  color: #ff6b08;
  transition: all .5s;
}

.header__nav-form {
  color: #fff;
  width: 280px;
  border-bottom: 1px solid;
  flex-direction: row;
  margin-top: 25px;
  display: flex;
  position: relative;
}

.header__nav-input {
  color: #fff;
  color: #ff6b08;
  background: none;
  border: none;
  outline: none;
  flex-grow: 2;
  padding: 4px;
  font-size: 14px;
}

.header__nav-input:focus + .header__nav-input-submit svg, .header__nav-input:hover + .header__nav-input-submit svg {
  stroke: #ff6b08;
}

.header__nav-input-submit {
  background-color: #0000;
  border: none;
  position: relative;
}

.header__nav-input--loupe {
  cursor: pointer;
  stroke: #fff;
  transition: all .5s;
}

.header__nav-input::placeholder {
  color: #fff;
}

.header__error {
  color: #ff001b;
  text-align: start;
  width: 280px;
  margin: 16px auto 0;
  padding: 0 12px;
  font-size: 14px;
  display: none;
}

.underline {
  border-bottom: 3px solid #ff001b;
}

.header.scrolled {
  height: 40px;
  transition: all .6s ease-in-out;
}

.scrolled-container {
  padding: 0;
}

.hovered {
  height: 216px;
  transition: all .6s ease-in-out;
}

.form-transform {
  transform: translateY(-10000px);
}

.nav-height {
  height: 40px;
  padding: 0;
}

@media screen and (min-width: 768px) {
  .header {
    height: 216px;
    background-image: url("header-background-tablet.3b543a43.jpg");
    background-size: 100vw 216px;
    margin-bottom: 10px;
  }

  .header .header:hover {
    height: 216px;
  }

  .header--library {
    background-image: url("my-library-tablet.b7e58955.jpg");
  }

  .header .header__nav-title {
    color: #fff;
    font-size: 30px;
    transition: all .5s ease-in-out;
    display: inline;
  }

  .header .header__nav-title:hover {
    color: #ff6b08;
  }

  .header .header__nav-container {
    width: 704px;
  }

  .header .header__nav-container > a {
    text-decoration: none;
  }

  .header__nav-form {
    width: 336px;
  }

  .header__error {
    width: 393px;
    text-align: center;
  }
}

.header:hover {
  height: 216px;
}

.header:hover .header__error {
  padding-left: 12px;
}

@media screen and (min-width: 1280px) {
  .header {
    background-image: url("header-background-desktop.0c40e61b.jpg");
  }

  .header--library {
    background-image: url("my-library-desktop.2b600886.jpg");
  }

  .header .header__nav-container {
    width: 1216px;
  }

  .header .header__nav-container > a {
    text-decoration: none;
  }

  .header__nav-form {
    width: 394px;
  }

  .header__error {
    width: 393px;
  }
}

